  .list-group-item:hover {
    transform: scale(1.02);
    background-color: #f1f1f1;
  }
  
  /* Icons */
  .notification-icon,
  .announcement-icon {
    font-size: 1.5rem;
    color: #007bff;
    margin-right: 15px;
    animation: pulse 1.5s infinite;
  }
  
  /* Animations */
  @keyframes pulse {
  
    0%,
    100% {
      transform: scale(1);
    }
  
    50% {
      transform: scale(1.1);
    }
  }
  
